const handleupdate=(g)=>{
 g.preventDefault();
 if (title===""||videourl==="") {
   alert("PLease Complete Title field and videourl")
 }else {
 if (sub_subject==="nil") {
   const updateref= doc(db,"Class",sclass,"subjects",subject,"lessons",lesson)
   updateDoc(updateref,{
      "title":title,
      "videourlone":videourl,
      "videourltwo":videourl1,
      "videourlthree":videourl2,
      "videourlfour":videourl3,
      "videourlfive":videourl4,
       "videourlsix":videourl5,
      "videourlseven":videourl6,
      "videourleight":videourl7,
      "videourlnine":videourl8,
      "videourlten":videourl9,
      "pdf":Url
      
   }).then(function() {
  alert("Data Updated")
   window.location.reload();
});
 }else{
    const updateref= doc(db,"Class",sclass,"subjects",subject,subject,sub_subject,"lessons",lesson)
   updateDoc(updateref,{
      "title":title,
      "videourlone":videourl,
      "videourltwo":videourl1,
      "videourlthree":videourl2,
      "videourlfour":videourl3,
      "videourlfive":videourl4,
      "videourlsix":videourl5,
      "videourlseven":videourl6,
      "videourleight":videourl7,
      "videourlnine":videourl8,
      "videourlten":videourl9,

      "pdf":Url
   }).then(function() {
  alert("Data Updated")
   window.location.reload();
});

 }
}
}
