const handledelete=(g)=>{

  if (sub_subject==="nil") {
  const desertRef = ref(storage,"pdf/"+title);

// Delete the file
  deleteObject(desertRef).then(() => {
  const loopconst =  parseInt(questionnumber);
     for (let i = 100; i < loopconst; i++) {
       
       deleteDoc(doc(db,"Class",sclass,"subjects",subject,"lessons",lesson,'test',i.toString()),)
     }

   const deleteref= doc(db,"Class",sclass,"subjects",subject,"lessons",lesson)
   deleteDoc(deleteref).then(function() {
     settitle("")
            setvideourl("")
            setvideourl1("")
            setvideourl2("")
            setvideourl3("")
            setvideourl4("")
  alert("Data Deleted")
  window.location.reload();
            
}).catch((error) => {
 alert("Error Try Again")
});

}).catch((error) => {
 alert("Error Try Again")
});
//})
 }else{
   const storage = getStorage();

// Create a reference to the file to delete
const desertRef = ref(storage,"pdf/"+title);

// Delete the file
deleteObject(desertRef).then(() => {
  // File deleted successfully
const loopconst =  parseInt(questionnumber);
     for (let i = 100; i < loopconst; i++) {
       
       deleteDoc(doc(db,"Class",sclass,"subjects",subject,subject,sub_subject,"lessons",lesson,'test',i.toString()),)
     }
  const deleteref= doc(db,"Class",sclass,"subjects",subject,subject,sub_subject,"lessons",lesson)
  deleteDoc(deleteref).then(function() {
  alert("Data Delete")
   }).then(function() {
     settitle("")
            setvideourl("")
            setvideourl1("")
            setvideourl2("")
            setvideourl3("")
            setvideourl4("")
  alert("Data Deleted")
  window.location.reload();
  
}).catch((error) => {
 alert("Error Try Again")
});
}).catch((error) => {
  // Uh-oh, an error occurred!
});
 }

}
