import React from 'react'
import { getStorage,ref,uploadBytes,getDownloadURL} from "firebase/storage";
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import MenuItem from '@material-ui/core/MenuItem';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import { db } from "./firebase"; 
import FormHelperText from '@material-ui/core/FormHelperText';

import {  updateDoc,doc} from "firebase/firestore"; 

import Select from '@material-ui/core/Select';




const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 200,
    marginLeft:-25,
    marginTop:20, 
  },
  selectEmpty: {
    marginTop: theme.spacing(10),
  },
  button: {
    margin: theme.spacing(1),
    marginTop:50,
    marginLeft:"70ch"
  },
   imagedesign: {
    margin: theme.spacing(1),
    marginTop:50,
    marginLeft:"70ch",
    maxWidth:100,
    maxHeight:100
  },
  text1: {
    '& .MuiTextField-root': {
      margin: theme.spacing(1),
      width: '40ch',
     marginTop:'5ch',
     marginLeft:'30ch',
    
      
    },
  },
  text2: {
    '& .MuiTextField-root': {
      margin: theme.spacing(0),
     width: '30ch',
     marginLeft:'10ch',
          },
  },
}));

const UploadBanner =()=>{
          const classes = useStyles();

        const [Url, setUrl] =React.useState('');
        const [probar,setprobar]=React.useState('*No Image Selected')
          const[title,settitle]=React.useState('');

   const onChange=(e)=>{  
    const storage = getStorage();
    const imagesRef=ref(storage,"bannerimages/"+title);

        const file=e.target.files[0];
        setprobar('50%')
        uploadBytes(imagesRef, file).then((snapshot) => {
        var progress = ("Image uploaded") ;
        setprobar(progress)

        console.log('Uploaded a blob or file!');
        getDownloadURL(ref(storage,"bannerimages/"+title))
          .then((url) => {
            setUrl(url);
            console.log(url)
      }, ).then((url)=>{
        url=""
        
      }) 
        });
      
  }
const upload=(g)=>{
  // g.preventDefault();
  const datas = {
    //  [title]:Url
    [title]:Url
      
    }

updateDoc(doc(db,"images","banner"),

datas
  
    ).then(()=>{
    alert("Banner Added");
    window.location.reload();
      // setloader(false);
    })
    .catch((error)=>{
      alert("error.message");
      // setloader(false);
    });

  }

   const handlevalidation=(g)=>{
 //g.preventDefault();
 if (Url===""||title===""){
     alert("Complete the fields")
 }else{
    upload();
 }
  

   }
        return (
         <div >
         <form>
         <div className={classes.button}> 
         <FormControl required className={classes.formControl}>
         <InputLabel id="demo-simple-select-required-label">Position</InputLabel>
         <Select
          labelId="demo-simple-select-required-label"
          id="demo-simple-select-required"
          value={title}
          onChange={(e)=>settitle(e.target.value)}
          className={classes.selectEmpty}
        >
          <MenuItem value="">
            <em>Position</em>
          </MenuItem>
           <MenuItem value={"imageurlone"}>One</MenuItem>
          <MenuItem value={"imageurltwo"}>Two</MenuItem>
          <MenuItem value={"imageurlthree"}>Three</MenuItem>
          <MenuItem value={"imageurlfour"}>Four</MenuItem>
          <MenuItem value={"imageurlfive"}>Five</MenuItem>
          
         
         
        </Select>
        <FormHelperText>Please Select Lesson</FormHelperText>
      </FormControl>
                   </div> 
                <div>{ title ? (
                  <div>
                  { Url ? (
                    <img src= {Url} className={classes.imagedesign}  alt="new"/>
                    ) : null }
                  <InputLabel id="demo-simple-select-required-label" className={classes.button} >{probar}</InputLabel>
                     <input type="file" className={classes.button}  onChange={onChange}/>
                     </div>
                      ) : null }
                </div>
                
                <div>
                  
      <Button
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        onClick={()=> handlevalidation()}
     >
        Update
      </Button>
                </div>
                </form>
            </div>
        )
    
}
export default UploadBanner;
