import React from 'react'
import Button from '@material-ui/core/Button';
import { useHistory} from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 200,
    marginLeft:100,
    marginTop:20, 
  },
  selectEmpty: {
    marginTop: theme.spacing(10),
  },
  button: {
    
    margin: theme.spacing(3),
    marginTop:60,
    backgroundColor: '#7B2572 ',
  
  },
  text1: {
    '& .MuiTextField-root': {
      margin: theme.spacing(1),
      width: '40ch',
     marginTop:'5ch',
     marginLeft:'30ch',
    
      
    },
  },
  text2: {
    '& .MuiTextField-root': {
      margin: theme.spacing(0),
     width: '30ch',
     marginLeft:'10ch',
          },
  },
}));


export default function Home() {
    const classes = useStyles();
    const history=useHistory();
    return (
        <div> 
          {/* <Button  color="inherit" onClick={()=> history.push("/uploadclass")} >Home</Button>
            &nbsp;&nbsp;&nbsp;&nbsp; */}
      <Button
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        style={{ background: '' }}
        
        onClick={()=>  history.push("/uploadclass")}
        >
        Add Class
        </Button>
        <Button
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        style={{ background: '' }}
        onClick={()=>  history.push("/editclass")}
        >
        Edit Class
        </Button>
        <Button
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        style={{ background: '' }}
        onClick={()=>  history.push("/uploadtest")}
        >
        Add Test
        </Button>
        <Button
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
       style={{ background: '' }}
        onClick={()=>  history.push("/updatetest")}
        >
        Update Test
        </Button>
         <Button
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        style={{ background: '' }}
        onClick={()=>  history.push("/uploadbanner")}
        >
        Add Banner
        </Button>
        <Button
        variant="contained"
        color="primary"
        size="large"
        className={classes.button}
        style={{ background: '' }}
        onClick={()=>  history.push("/softskillsupload")}
        >
        Soft Skills
        </Button>
           
        </div>
    )
}
